@mixin fontSmoothing() {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.clearfix::after {
  content: " ";
  display: block;
  clear: both;
}

.centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.d-ib {
  display: inline-block;
}

.d-b {
  display: block;
}

.d-tc {
  display: table-cell;
}

.p-relative {
  position: relative;
}

.p-absolute {
  position: absolute;
}

.p-fixed {
  position: fixed;
}

.p-sticky-t {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  z-index: 1;
}

.b-0 {
  bottom: 0;
}

.t-0 {
  top: 0;
}

.b-20 {
  bottom: 20px;
}

.p-sticky-b {
  position: sticky;
  position: -webkit-sticky;
  bottom: 0;
}

.bd {
  border: dashed 1px #eaeaea;
}

.ba {
  border: solid 1px #eaeaea;
}

.bb {
  border-bottom: solid 1px #eaeaea;
}

.bt {
  border-top: solid 1px #eaeaea;
}

.bl {
  border-left: solid 1px #eaeaea;
}

.br {
  border-right: solid 1px #eaeaea;
}

.bb-dashed {
  border-bottom: dashed 1px #eaeaea;
}

.bb-0 {
  border-bottom: 0 !important;
}

.bt-0 {
  border-top: 0 !important;
}

.bl-0 {
  border-left: 0 !important;
}

.br-0 {
  border-right: 0 !important;
}

.br0 {
  border-radius: 0;
}

.br1 {
  border-radius: 0.125rem;
}

.br2 {
  border-radius: 0.25rem;
}

.br3 {
  border-radius: 0.5rem;
}

.br4 {
  border-radius: 1rem;
}

.br100 {
  border-radius: 100%;
}

.br-pill {
  border-radius: 9999px;
}

.btr0 {
  border-top-right-radius: 0px;
}

.btl0 {
  border-top-left-radius: 0px;
}

.bbr0 {
  border-bottom-right-radius: 0px;
}

.bbl0 {
  border-bottom-left-radius: 0px;
}

.o-0 {
  opacity: 0;
}

.o-20 {
  opacity: 0.2;
}

.o-40 {
  opacity: 0.4;
}

.o-50 {
  opacity: 0.5;
}

.o-80 {
  opacity: 0.8;
}

.o-100 {
  opacity: 1;
}

.overflow-x-scroll {
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
}

.hide-scrollbar {
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
}

.hide-scrollbar::-webkit-scrollbar {
  display: none;
}

.overflow-x-noscroll {
  overflow-x: hidden;
}

.overflow-y-scroll {
  overflow-y: scroll;
  // -webkit-overflow-scrolling: touch;
}

.overflow-unset {
  overflow: unset;
}

.overflow-y-noscroll {
  overflow-y: hidden;
}

.ws-nowrap {
  white-space: nowrap;
}

.vertical-top {
  vertical-align: top;
}

.vertical-bottom {
  vertical-align: bottom;
}

.body--modal-open {
  overflow: hidden;
}

.vertical-line-divider {
  border-left: solid;
  border-width: 1px;
  border-color: #ddd;
  margin-left: 10px;
}

.c-pointer-none {
  pointer-events: none;
}

a {
  text-decoration: none;

  &.disabled {
    pointer-events: none;
    cursor: none;
  }
}

.z-1 {
  z-index: 1;
}

.autocompleteDiv {
  position: absolute;
  z-index: 1;
  background-color: #ffffff;
  color: #000000;
  border: 0.5px solid #ededed;
  width: 230px;
  overflow: hidden;
  left: 80px;
  top: 35px;
  box-shadow: 2px 2px #ededed;
  border-radius: 0px 0px 5px 5px;

  li {
    &:hover {
      background-color: #ededed;
    }
  }
}

.r-70 {
  right: 70px;
}
